import React, { useEffect, useState } from 'react';
import { Button, ProgressLoadingBar } from '@ohif/ui'

export default function PanoramicGenerationPanel({ commandsManager, servicesManager }): React.FunctionComponent {
  const { panoramicService, viewportGridService } = servicesManager.services;

  const [panoramicState, setPanoramicState] = useState(
    panoramicService.getPanoramicState(viewportGridService.getDisplaySetsUIDsForViewport('default')?.[0]) ?? null
  );
  const [statusDisplayed, setStatusDisplayed] = useState(panoramicService.isDisplayed());
  // Progress bar for panoramic computation
  const [statusEditing, setStatusEditing] = useState(panoramicService.isEditing());
  const [processingPercent, setProcessingPercent] = useState(0);

  useEffect(() => {
    const events = [
      {
        event: panoramicService.EVENTS.PANORAMIC_STATE_UPDATED,
        callback: ({ displaySetUID }) => setPanoramicState({ ...panoramicService.getPanoramicState(displaySetUID) })
      },
      {
        event: panoramicService.EVENTS.PANORAMIC_PROCESSING,
        callback: ({ percent }) => setProcessingPercent(percent)
      },
      {
        event: panoramicService.EVENTS.PANORAMIC_CREATED,
        callback: () => setProcessingPercent(0)
      },
      {
        event: panoramicService.EVENTS.STATUS_DISPLAYED_CHANGED,
        callback: ({ displayed }) => setStatusDisplayed(displayed)
      },
      {
        event: panoramicService.EVENTS.STATUS_EDITING_CHANGED,
        callback: ({ editing }) => setStatusEditing(editing)
      }
    ];

    const subscriptions = events.map(
      ({ event, callback }) => panoramicService.subscribe(event, callback).unsubscribe
    );

    return () => {
      for (const unsub of subscriptions) {
        unsub();
      }
    }
  }, []);

  const toggleAnnotation = () => {
    const cpAnnotationUID = panoramicState.annotationUIDs[0];
    panoramicService.setAnnotationVisibility(panoramicState.id, [cpAnnotationUID], !statusEditing);
    panoramicService.setAnnotationVisibility(panoramicState.id, panoramicState.annotationUIDs.slice(1), statusEditing);
    // Validating control points coordinates
    if (statusEditing) {
      panoramicService.computePanoramic(panoramicState.id);
    }
    panoramicService.setEditing(!statusEditing);
  }

  const toggleHangingProtocol = () => {
    commandsManager.runCommand('toggleHangingProtocol', { protocolId: 'panoramicViewport', stageIndex: 1 });
  }

  // @ts-ignore
  return (
    <div style={{width:'100%', height:'100%'}}>
      <div style={{width:'100%', display:'flex', flexDirection:'column', height:'100%'}}>
        <p style={{color:'white', fontWeight:'bold', marginBottom:'10px'}}>{'Panoramic Generation'}</p>
        { statusDisplayed &&
          <Button onClick={toggleAnnotation}>{!statusEditing ? 'Move control points' : 'Validate'}</Button>
        }
        <div className="h-[6px] bg-black "></div>
        <Button onClick={toggleHangingProtocol}>{'Dentascan Focus'}</Button>
        { processingPercent &&
          <div className={'w-full mt-[10px] mb-[20px]'}>
            <p
              style={{color:'white', fontSize:'small', marginBottom:'10px', marginTop: '10px'}}
            >{'Creating panoramic...'}</p>
            <ProgressLoadingBar progress={processingPercent}/>
          </div>
        }
      </div>
    </div>
  );
}
